.ff__circle-thin {
  font-weight: 100;
}

.ff__circle-light {
  font-weight: 300;
}

.ff__circle-bold {
  font-weight: bold;
}

.ff__circle-extra-bold {
  font-weight: 800;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

a,
button,
input,
input::placeholder,
textarea,
textarea::placeholder {
  font-family: "RalewayDev", "MontserratDev", "sans serif";
}

body {
  margin: 0;
  min-width: 320px;
  overflow-x: hidden;
  font-family: "RalewayDev", "MontserratDev", "sans serif";
  color: #2C2C2C;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

a:hover,
button:hover,
input:hover {
  cursor: pointer;
}

.container {
  width: 100%;
  max-width: calc(1320px + 40px);
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
}

.text {
  font-size: 15px;
  line-height: 150%;
  color: #2C2C2C;
}

.text_type-1 {
  font-weight: bold;
  font-size: 13px;
  line-height: 150%;
}

.text_type-2 {
  font-size: 12px;
  line-height: 150%;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #a0a0a0;
}

.text_type-3 {
  font-size: 15px;
  line-height: 150%;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #5C5C5C;
}

.ttu {
  text-transform: uppercase;
}

.ui-input {
  border: 0;
  outline: 0;
  font-size: 13px;
  line-height: 19px;
  height: 40px;
}

.ui-input::placeholder {
  font-size: 13px;
  line-height: 19px;
}

.ui-input_icon {
  padding-right: 40px;
}

.ui-card__container {
  display: flex;
}

.ui-card__img {
  max-height: 133px;
  width: auto;
  display: block;
}

.ui-card__content {
  display: flex;
  flex-direction: column;
  margin-left: 97px;
}

.ui-card__name {
  margin-bottom: 5px;
}

.ui-card__code {
  margin-bottom: 22px;
}

.ui-card__sizes-and-colors {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
}

.ui-card__sizes {
  display: flex;
  align-items: center;
  margin-right: 20px;
}

.ui-card__colors {
  display: flex;
  align-items: center;
}

.ui-card__color {
  position: relative;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 1px solid #A0A0A0;
  background-color: var(--bgColor);
}

.ui-card__price {
  margin-top: auto;
  font-weight: 600;
  font-size: 13px;
  line-height: 150%;
  letter-spacing: 0.15em;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #000000;
}

.top-message {
  background-color: #2C2C2C;
}

.top-message__container {
  height: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.top-message__text {
  font-weight: 300;
  font-size: 13px;
  color: #fff;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 65px;
}

.header__left {
  display: flex;
  align-items: center;
}

.header__menu {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 22px;
  min-width: 22px;
  flex: 0 0 22px;
  margin-right: 15px;
  border: 0;
  outline: 0;
  background-color: transparent;
}

.header__menu.active .header__menu-icon-bar {
  display: none;
}

.header__menu.active .header__menu-icon-close {
  display: block;
}

.header__menu-icon-bar {
  pointer-events: none;
}

.header__menu-icon-close {
  display: none;
  pointer-events: none;
}

.header__items {
  display: flex;
  align-items: center;
}

.header__items_left {
  margin-right: 40px;
}

.header__items_right {
  margin-right: 40px;
}

.header__item {
  text-decoration: none;
}

.header__item:not(:last-child) {
  margin-right: 40px;
}

.header__item_phone {
  font-feature-settings: "pnum" on, "lnum" on;
}

.header__search {
  position: relative;
  padding-right: 20px;
  border-bottom: 1px solid transparent;
  transition: all 0.3s ease;
  cursor: pointer;
}

.header__search.active {
  padding-right: 40px;
  border-bottom-color: #2C2C2C;
}

.header__search.active .header__input {
  width: 200px;
  max-width: none;
  flex: 0 0 200px;
}

.header__input {
  width: 0;
  max-width: 0;
  flex: 0 0 0;
  transition: all 0.3s ease;
}

.header__input-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
}

.header__right {
  display: flex;
  align-items: center;
}

.header__navigation {
  position: absolute;
  top: calc(65px + 28px);
  height: calc(100vh - (65px + 28px));
  left: 0;
  right: 0;
  width: 100%;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  transition: all 0.3s ease;
}

.header__navigation.active {
  opacity: 1;
  visibility: visible;
  z-index: 100;
}

.header__navigation-all-items {
  display: flex;
  justify-content: space-between;
  padding: 30px 30px 80px 0;
  width: 50%;
}

.header__navigation-items:not(:last-child) {
  margin-right: 45px;
}

.header__navigation-item {
  margin-bottom: 10px;
}

.header__navigation-item:last-child {
  margin-bottom: 0;
}

.header__navigation-item_title {
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 14px;
  line-height: 15px;
}

.header__navigation-item_link {
  display: block;
  color: #A0A0A0;
}

.header__navigation-item-icon {
  display: none;
}

.header__results {
  position: absolute;
  top: calc(65px + 28px);
  height: calc(100vh - (65px + 28px));
  left: 0;
  right: 0;
  width: 100%;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  transition: all 0.3s ease;
}

.header__results.active {
  opacity: 1;
  visibility: visible;
  z-index: 100;
}

.header__results-items {
  padding: 25px 30px 0 20px;
  width: 50%;
}

.header__results-card:not(:last-child) {
  margin-bottom: 40px;
}

@media (max-width: 991px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }

  .text {
    font-size: 13px;
    line-height: 19px;
  }
}